import { request } from 'utils';

const prefix = '/api/categories';

export const getBuildings = async () =>
  request({
    prefix,
    url: '/Building',
    params: {},
    method: 'GET',
  });

export const getBuildingBases = async () =>
  request({
    prefix,
    url: '/BuildingBase',
    params: {},
    method: 'GET',
  });

export const getBuildingById = async id =>
  request({
    prefix,
    url: `/Building/GetById`,
    params: { id },
    method: 'GET',
  });

export const createBuildings = async values =>
  request({
    prefix,
    url: '/Building',
    params: {},
    data: { ...values },
    method: 'POST',
  });

export const updateBuildings = async values =>
  request({
    prefix,
    url: '/Building',
    params: {},
    data: { ...values },
    method: 'PUT',
  });

export const deleteBuilding = async id =>
  request({
    prefix,
    url: '/Building',
    params: {},
    data: { id },
    method: 'DELETE',
  });



